<template>
  <div v-if="show" class="modal-overlay" @click.self="$emit('close')">
    <div class="modal-content" :class="type">
      <button class="close-button" @click="$emit('close')">×</button>
      <h2>{{ title }}</h2>
      
      <div v-if="type === 'error'" class="modal-icon error-icon">
        <i class="fas fa-exclamation-circle"></i>
      </div>
      
      <div v-else-if="type === 'success'" class="modal-icon success-icon">
        <i class="fas fa-check-circle"></i>
      </div>
      
      <div v-else-if="type === 'info'" class="modal-icon info-icon">
        <i class="fas fa-info-circle"></i>
      </div>
      
      <div v-else-if="type === 'warning'" class="modal-icon warning-icon">
        <i class="fas fa-exclamation-triangle"></i>
      </div>
      
      <div class="modal-body">
        <slot></slot>
      </div>
      
      <div class="modal-footer" v-if="$slots.footer">
        <slot name="footer"></slot>
      </div>
      
      <div class="modal-footer" v-else-if="primaryButtonText">
        <button class="primary-button" @click="$emit('primary-action')">
          {{ primaryButtonText }}
        </button>
        <button v-if="secondaryButtonText" class="secondary-button" @click="$emit('secondary-action')">
          {{ secondaryButtonText }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AppModal',
  props: {
    show: {
      type: Boolean,
      required: true
    },
    title: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'default',
      validator: (value) => ['default', 'success', 'error', 'warning', 'info'].includes(value)
    },
    primaryButtonText: {
      type: String,
      default: ''
    },
    secondaryButtonText: {
      type: String,
      default: ''
    }
  },
  emits: ['close', 'primary-action', 'secondary-action']
}
</script>

