<script>
import { ref, computed, onMounted } from 'vue';
import axios from 'axios';
import WalletScanForm from '@/components/cashback/WalletScanForm.vue';
import ScanResults from '@/components/cashback/ScanResults.vue';
import BenefitsSection from '@/components/cashback/BenefitsSection.vue';
import ReferralInfo from '@/components/cashback/ReferralInfo.vue';
import FAQ from '@/components/cashback/FAQ.vue';
import store from '@/vuex';

export default {
  name: 'CashbackPage',
  components: {
    WalletScanForm,
    ScanResults,
    BenefitsSection,
    ReferralInfo,
    FAQ
  },
  setup() {
    const showHowItWorks = ref(false);
    const scanResults = ref(null);
    const currentWalletAddress = ref('');
    const showSuccessModal = ref(false);
    const claimedAmount = ref(0);
    const closedAccountsCount = ref(0);
    const sentSignature = ref('');
    const statsData = ref({
      solAmount: '0.00',
      ataAmount: 0
    });
    
    // Функция для получения статистики
    const fetchStats = async () => {
      try {
        const response = await axios.get('/ata-cashback/stats');
        if (response.data.success && response.data.stats) {
          statsData.value = response.data.stats;
        }
      } catch (error) {
        console.error('Error fetching ATA cashback stats:', error);
      }
    };
    
    // Загружаем статистику при монтировании компонента
    onMounted(() => {
      fetchStats();
    });

    const handleScanComplete = (result) => {
      scanResults.value = result.accounts;
      currentWalletAddress.value = result.walletAddress;
    };

    const resetScan = () => {
      scanResults.value = null;
      currentWalletAddress.value = '';
    };

    const handleClaimCashback = (result) => {
      claimedAmount.value = result.amount;
      closedAccountsCount.value = result.accountsCount;
      sentSignature.value = result.sentSignature;
      showSuccessModal.value = true;
    };

    const formatSol = (amount) => {
      return amount.toFixed(4);
    };

    const shareOnTwitter = () => {
      const text = `I just reclaimed ${formatSol(claimedAmount.value)} SOL from Associated Token Accounts using $BITCAT's ATA #Cashback tool! Check it out at https://solanabitcat.com/#/cashback${store.getters.isAuthenticated ? `?r=${store.getters.userTag}` : ''}`;
      window.open(`https://twitter.com/intent/tweet?text=${encodeURIComponent(text)}`, '_blank');
    };
    
    // Обновление списка аккаунтов после удаления использованных
    const updateAccounts = (updatedAccounts) => {
      scanResults.value = updatedAccounts;
      fetchStats();
    };

    return {
      showHowItWorks,
      scanResults,
      currentWalletAddress,
      handleScanComplete,
      resetScan,
      handleClaimCashback,
      showSuccessModal,
      claimedAmount,
      closedAccountsCount,
      formatSol,
      shareOnTwitter,
      updateAccounts,
      sentSignature,
      statsData
    };
  }
};
</script>

