<template>
  <div class="wallet-scan-form">
    <h2>Scan Your Wallet for Reclaimable SOL</h2>
    
    <div class="form-container">
      <div class="input-group">
        <input 
          type="text" 
          v-model="walletAddress" 
          placeholder="Enter your Solana wallet address" 
          :disabled="loading"
          class="wallet-input"
        />
        <button 
          @click="scanWallet" 
          :disabled="!isValidAddress || loading" 
          class="scan-button"
        >
          <span v-if="!loading">Scan Wallet</span>
          <span v-else class="loading-spinner">
            <i class="fas fa-spinner fa-spin"></i>
          </span>
        </button>
      </div>
      
      <div v-if="error" class="error-message">
        {{ error }}
      </div>
      
      <div class="recent-history-section">
        <RecentHistory />
        
        <div class="view-all-history">
          <router-link to="/cashback/history" class="history-button">
            <i class="fas fa-history"></i> View Full Transaction History
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue';
import RecentHistory from './RecentHistory.vue';
import { PublicKey } from '@solana/web3.js';
import store from '@/vuex';
import axios from 'axios';

export default {
  name: 'WalletScanForm',
  components: {
    RecentHistory
  },
  emits: ['scan-complete'],
  setup(props, { emit }) {
    const walletAddress = ref('');
    const loading = ref(false);
    const error = ref('');

    // Check if address is valid Solana public key
    const isValidAddress = computed(() => {
      if (!walletAddress.value) return false;
      
      try {
        new PublicKey(walletAddress.value);
        return true;
      } catch (err) {
        return false;
      }
    });

    const connectWallet = async () => {
      try {
        // Check if Phantom is installed
        if (!window.solana || !window.solana.isPhantom) {
          error.value = 'Phantom wallet not found. Please install Phantom extension.';
          return;
        }

        // Connect to wallet
        await window.solana.connect();
        walletAddress.value = window.solana.publicKey.toString();
        
        // Auto scan after connecting
        scanWallet();
      } catch (err) {
        error.value = `Error connecting wallet: ${err.message}`;
        console.error('Wallet connection error:', err);
      }
    };

    const scanWallet = async () => {
      if (!isValidAddress.value) {
        error.value = 'Please enter a valid Solana wallet address';
        return;
      }

      loading.value = true;
      error.value = '';

      try {
        const response = await axios.get(`/ata-cashback/scan/${walletAddress.value}`);
        
        if (response.data.success) {
          emit('scan-complete', {
            walletAddress: walletAddress.value,
            accounts: response.data.accounts
          });
        } else {
          error.value = response.data.message || 'Failed to scan wallet';
        }
      } catch (err) {
        error.value = err.response?.data?.message || 'Error scanning wallet';
        console.error('Scan error:', err);
      } finally {
        loading.value = false;
      }
    };

    // If user is already logged in with wallet, use that address
    const userWallet = computed(() => store.getters.walletAddress);
    if (userWallet.value) {
      walletAddress.value = userWallet.value;
    }

    return {
      walletAddress,
      loading,
      error,
      isValidAddress,
      scanWallet
    };
  }
};
</script>

