<template>
  <div class="referral-info">
    <div class="referral-card">
      <h3>Earn SOL with Our Referral Program</h3>
      <p>Share this tool with your friends and earn up to 5% of their reclaimed SOL. The more people you refer, the more you earn!</p>
      
      <div v-if="referralLink" class="referral-link-container">
        <div class="referral-link">
          <input type="text" readonly :value="referralLink" ref="referralLinkInput" />
          <button @click="copyReferralLink" class="copy-button">
            <i :class="['fas', copied ? 'fa-check' : 'fa-copy']"></i>
          </button>
        </div>
        <div v-if="copied" class="copied-message">Copied to clipboard!</div>
      </div>
      
      <router-link v-else to="/my/referrals" class="referral-button">
        <i class="fas fa-link"></i>Get Referral Link
      </router-link>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
import { useStore } from 'vuex';

export default {
  name: 'ReferralInfo',
  setup() {
    const store = useStore();
    const referralLink = ref('');
    const copied = ref(false);
    const referralLinkInput = ref(null);
    
    const generateReferralLink = async () => {
      // If user is logged in, use their userTag as referral code
      const userTag = store.state.auth?.userTag;
      
      if (userTag) {
        referralLink.value = `${window.location.origin}/cashback?ref=${userTag}`;
      } else {
        // If not logged in, generate a temporary referral code
        // This could be based on wallet address if connected
        const walletAddress = window.solana?.publicKey?.toString();
        
        if (walletAddress) {
          // Use first 8 characters of wallet address as referral code
          const refCode = walletAddress.substring(0, 8);
          referralLink.value = `${window.location.origin}/cashback?ref=${refCode}`;
        } else {
          // Prompt to connect wallet or login
          alert('Please connect your wallet or login to generate a referral link');
        }
      }
    };
    
    const copyReferralLink = () => {
      if (referralLinkInput.value) {
        referralLinkInput.value.select();
        document.execCommand('copy');
        copied.value = true;
        
        // Reset copied state after 3 seconds
        setTimeout(() => {
          copied.value = false;
        }, 3000);
      }
    };
    
    // Check for existing referral code in URL
    const checkExistingReferral = () => {
      const urlParams = new URLSearchParams(window.location.search);
      const refCode = urlParams.get('ref');
      
      if (refCode) {
        // Store the referral code in localStorage or state
        localStorage.setItem('cashbackReferrer', refCode);
      }
    };
    
    // Call on component mount
    checkExistingReferral();
    
    return {
      referralLink,
      copied,
      referralLinkInput,
      generateReferralLink,
      copyReferralLink
    };
  }
};
</script>

