<template>
  <div class="scan-results">
    <h2>Your ATA Accounts</h2>
    <div class="wallet-address-display">
      <span class="address-label">For wallet:</span> <span class="address-value">{{ shortenAddress(walletAddress, 8) }}</span>
    </div>
    
    <div v-if="loading" class="loading-container">
      <div class="loading-spinner">
        <i class="fas fa-spinner fa-spin"></i>
      </div>
      <p>Processing your accounts...</p>
    </div>
    
    <div v-else-if="accounts.length === 0" class="no-accounts">
      <i class="fas fa-check-circle"></i>
      <p>No unused token accounts found for this wallet.</p>
      <button @click="$emit('reset')" class="reset-button">
        Scan Another Wallet
      </button>
    </div>
    
    <div v-else class="accounts-container">
      <div class="summary-card">
        <div class="summary-item">
          <div class="summary-label">Total Accounts</div>
          <div class="summary-value">{{ accounts.length }}</div>
        </div>
        
        <div class="summary-item">
          <div class="summary-label">Empty Accounts</div>
          <div class="summary-value">{{ emptyAccounts.length }}</div>
        </div>
        
        <div class="summary-item">
          <div class="summary-label">With Balance</div>
          <div class="summary-value">{{ accountsWithBalance.length }}</div>
        </div>
        
        <div class="summary-item">
          <div class="summary-label">Reclaimable SOL</div>
          <div class="summary-value">{{ totalReclaimableSol }} SOL</div>
        </div>
        <div class="summary-item">
          <div class="summary-label">Receive SOL</div>
          <div class="summary-value highlight">{{ activeTab === 'withBalance' ? selectedBalanceReclaimableSol : selectedReclaimableSol }} SOL</div>
        </div>

        <div class="summary-item wallet-button-item desktop-only">
          <wallet-multi-button></wallet-multi-button>
        </div>
      </div>
      
      <!-- Мобильная версия кнопки кошелька -->
      <div class="mobile-wallet-button mobile-only">
        <wallet-multi-button></wallet-multi-button>
      </div>
      
      <div class="actions">
        <button @click="$emit('reset')" class="reset-button">
          Back
        </button>
        <button 
          v-if="activeTab === 'empty'"
          @click="claimCashback" 
          :disabled="selectedCount === 0 || processing"
          class="claim-button"
        >
          <span v-if="!processing">Reclaim {{ selectedReclaimableSol }} SOL</span>
          <span v-else class="loading-spinner">
            <i class="fas fa-spinner fa-spin"></i> Processing
          </span>
        </button>
        <button 
          v-if="activeTab === 'withBalance'"
          @click="burnAndClaimCashback" 
          :disabled="selectedBalanceCount === 0 || processingBurn"
          class="burn-claim-button"
        >
          <span v-if="!processingBurn">Burn & Reclaim {{ selectedBalanceReclaimableSol }} SOL</span>
          <span v-else class="loading-spinner">
            <i class="fas fa-spinner fa-spin"></i> Processing
          </span>
        </button>
      </div>
      
    
      <!-- Tabs Navigation -->
      <div class="tabs-navigation">
        <button 
          class="tab-button" 
          :class="{ 'active': activeTab === 'empty' }" 
          @click="activeTab = 'empty'"
        >
          Empty ({{ emptyAccounts.length }})
        </button>
        <button 
          class="tab-button" 
          :class="{ 'active': activeTab === 'withBalance' }" 
          @click="activeTab = 'withBalance'"
        >
          With Balance ({{ accountsWithBalance.length }})
        </button>
      </div>
    
      <!-- Empty Accounts Tab Content -->
      <div v-if="activeTab === 'empty'" class="tab-content">
        <div class="accounts-grid-header">
          <div class="select-all">
            <input 
              type="checkbox" 
              id="select-all" 
              v-model="selectAll"
              @change="toggleSelectAll"
            >
            <label for="select-all"> Select</label>
          </div>
          <div>Mint</div>
          <div>Balance</div>
          <div>Rent</div>
        </div>
      
      <div class="accounts-grid">
        <div 
          v-for="(account, index) in displayedEmptyAccounts" 
          :key="account.mint"
          class="account-card"
        >
          <div class="account-select">
            <input 
              type="checkbox" 
              :id="`account-${index}`" 
              v-model="selectedAccounts[getOriginalIndex(account)]"
              @change="handleAccountSelection(getOriginalIndex(account))"
            >
          </div>
          <div class="account-mint">
            <div class="address-display" :title="account.mint">
              {{ shortenAddress(account.mint) }}
            </div>
            <a :href="`https://solscan.io/account/${account.ataAddress}`" target="_blank" class="explorer-link">
              <i class="fas fa-external-link-alt"></i>
            </a>
          </div>
          <!-- balance -->
          <div class="account-balance">
            {{ parseFloat(account.amount / 1000000).toFixed(0) }}
            <span v-if="account.amount > 100000000" class="warning-icon" title="This account has too many tokens to burn">
              ⚠️
            </span>
          </div>
          <div class="account-rent">
            {{ rentAmount.toFixed(3) }} SOL
          </div>
        </div>
      </div>
      
      <!-- Pagination for Empty Accounts -->
      <div class="pagination">
        <div class="pagination-info">
          Showing {{ displayedEmptyAccounts.length }} of {{ emptyAccounts.length }} accounts
          <span v-if="displayedEmptyAccounts.length < emptyAccounts.length" class="hidden-count">
            ({{ emptyAccounts.length - displayedEmptyAccounts.length }} more hidden)
          </span>
        </div>
        <button 
          v-if="displayedEmptyAccounts.length < emptyAccounts.length" 
          @click="loadMoreEmptyAccounts" 
          class="load-more-button"
        >
          Show More
        </button>
      </div>
      
      <div v-if="emptyAccounts.length === 0" class="no-accounts">
        No empty token accounts found for this wallet.
      </div>
      
      <div class="actions" v-if="selectedCount > 0">
        <button 
          @click="claimCashback" 
          :disabled="selectedCount === 0 || processing"
          class="claim-button"
        >
          <span v-if="!processing">Reclaim {{ selectedReclaimableSol }} SOL</span>
          <span v-else class="loading-spinner">
            <i class="fas fa-spinner fa-spin"></i> Processing
          </span>
        </button>
      </div>
    </div>
    
    <!-- Accounts With Balance Tab Content -->
    <div v-if="activeTab === 'withBalance'" class="tab-content">
      <div class="warning-message">
        <i class="fas fa-exclamation-triangle"></i>
        <p><strong>Warning:</strong> You are about to burn tokens and close accounts. This action cannot be undone. Please verify that you don't need these tokens anymore.</p>
      </div>
      <div>
        <div class="accounts-grid-header">
          <div class="select-all">
            <input 
              type="checkbox" 
              id="select-all-balance" 
              v-model="selectAllWithBalance"
              @change="toggleSelectAllWithBalance"
            >
            <label for="select-all-balance"> Select</label>
          </div>
          <div>Mint</div>
          <div>Balance</div>
          <div>Rent</div>
        </div>
        
        <div class="accounts-grid">
          <div 
            v-for="(account, index) in displayedBalanceAccounts" 
            :key="account.mint"
            class="account-card with-balance"
          >
            <div class="account-select">
              <input 
                type="checkbox" 
                :id="`account-balance-${index}`" 
                v-model="selectedBalanceAccounts[getOriginalIndex(account)]"
                @change="handleBalanceAccountSelection(getOriginalIndex(account))"
                :disabled="account.amount > 100000000 || prepareStablecoin(account.mint) !== ''"
              >
            </div>
            <div class="account-mint">
              <div class="address-display" :title="account.mint">
                {{ shortenAddress(account.mint) }}<small v-if="prepareStablecoin(account.mint) !== ''">{{ prepareStablecoin(account.mint) }}</small>
              </div>
              <a :href="`https://solscan.io/account/${account.ataAddress}`" target="_blank" class="explorer-link">
                <i class="fas fa-external-link-alt"></i>
              </a>
            </div>
            <div class="account-balance">
              {{ parseFloat(account.amount / 1000000).toFixed(4) }}
              <span v-if="account.amount > 100000000" class="warning-icon" title="This account has too many tokens to burn">
                ⚠️
              </span>
            </div>
            <div class="account-rent">
              {{ rentAmount.toFixed(3) }} SOL
            </div>
          </div>
        </div>
        
        <!-- Pagination for Accounts With Balance -->
        <div class="pagination">
          <div class="pagination-info">
            Showing {{ displayedBalanceAccounts.length }} of {{ accountsWithBalance.length }} accounts
            <span v-if="displayedBalanceAccounts.length < accountsWithBalance.length" class="hidden-count">
              ({{ accountsWithBalance.length - displayedBalanceAccounts.length }} more hidden)
            </span>
          </div>
          <button 
            v-if="displayedBalanceAccounts.length < accountsWithBalance.length" 
            @click="loadMoreBalanceAccounts" 
            class="load-more-button"
          >
            Show More
          </button>
        </div>

        <div class="balance-actions" v-if="selectedBalanceCount > 30">
          <div class="warning-message">
            <i class="fas fa-exclamation-triangle"></i>
            <p><strong>Warning:</strong> You are about to burn tokens and close accounts. This action cannot be undone. Please verify that you don't need these tokens anymore.</p>
          </div>
          
          <button 
            @click="burnAndClaimCashback" 
            :disabled="selectedBalanceCount === 0 || processingBurn"
            class="burn-claim-button"
          >
            <span v-if="!processingBurn">Burn & Reclaim {{ selectedBalanceReclaimableSol }} SOL</span>
            <span v-else class="loading-spinner">
              <i class="fas fa-spinner fa-spin"></i> <span class="processing-text">Processing</span>
            </span>
          </button>
        </div>
        
        <div v-if="accountsWithBalance.length === 0" class="no-accounts">
          No token accounts with balance found for this wallet.
        </div>
      </div>
    </div>
    
    <div class="fee-info">
      <p>
        <strong>Fee:</strong> 20% (5% referral commission, 15% project development)
        <br>
        <span class="vip-note">BITCAT holders receive a discounted fee of only 10%. At least 1000 BITCAT to get a discount</span>
      </p>
    </div>
    
    <!-- Error Modal -->
    <AppModal
      :show="showErrorModal"
      title="Error"
      type="error"
      primaryButtonText="Try Again"
      @close="showErrorModal = false"
      @primary-action="showErrorModal = false"
    >
      <p>{{ errorMessage }}</p>
    </AppModal>
    
    <!-- Limit Warning Modal -->
    <AppModal
      :show="showLimitWarningModal"
      title="Transaction Limit"
      type="warning"
      primaryButtonText="Got it"
      @close="showLimitWarningModal = false"
      @primary-action="showLimitWarningModal = false"
    >
      <p>
        Due to Solana blockchain limitations, you can only close up to {{ MAX_ACCOUNTS_PER_TX }} token accounts in a single transaction.
        <br><br>
        Please select no more than {{ MAX_ACCOUNTS_PER_TX }} accounts to close at once.
      </p>
    </AppModal>
    
    <!-- Burn Warning Modal -->
    <AppModal
      :show="showBurnWarningModal"
      title="Warning: Burning Tokens"
      type="warning"
      primaryButtonText="Proceed"
      secondaryButtonText="Cancel"
      @close="showBurnWarningModal = false"
      @primary-action="confirmBurnAndClaimCashback"
      @secondary-action="showBurnWarningModal = false"
    >
      <p>
        <strong>Warning:</strong> You are about to burn tokens and close accounts. This action cannot be undone.
        <br><br>
        Due to Solana blockchain limitations, you can only burn and close up to {{ MAX_BURN_ACCOUNTS_PER_TX }} token accounts in a single transaction.
        <br><br>
        Please verify that you don't need these tokens anymore before proceeding.
      </p>
    </AppModal>

    <AppModal
      :show="showBurnLimitsWarningModal"
      title="Transaction Limit"
      type="warning"
      primaryButtonText="Got it"
      @close="showBurnLimitsWarningModal = false"
      @primary-action="showBurnLimitsWarningModal = false"
    >
    <p>
        Due to Solana blockchain limitations, you can only burn & close up to {{ MAX_BURN_ACCOUNTS_PER_TX }} token accounts in a single transaction.
        <br><br>
        Please select no more than {{ MAX_BURN_ACCOUNTS_PER_TX }} accounts to burn & close at once.
      </p>
    </AppModal>

    <AppModal
      :show="showWalletAddressMismatchModal"
      title="Wallet Address Mismatch"
      type="warning"
      primaryButtonText="Got it"
      @close="showWalletAddressMismatchModal = false"
      @primary-action="showWalletAddressMismatchModal = false"
    >
    <p>
        The wallet address you are trying to use does not match the wallet address you are connected to.
        <br><br>
        Please connect to the correct wallet address.
      </p>
    </AppModal>
  </div>
</div>
    
  
</template>

<script>
import { WalletMultiButton } from 'solana-wallets-vue'
import { ref, computed, onMounted, watch } from 'vue';
import store from '@/vuex';
import axios from 'axios';
import { Transaction, PublicKey, LAMPORTS_PER_SOL } from '@solana/web3.js';
import AppModal from '../common/Modal.vue';
import { createCloseAccountInstruction } from '@solana/spl-token';
import { useWallet } from 'solana-wallets-vue';

export default {
  name: 'ScanResults',
  components: {
    AppModal,
    WalletMultiButton
  },
  props: {
    accounts: {
      type: Array,
      required: true
    },
    walletAddress: {
      type: String,
      required: true
    }
  },
  emits: ['reset', 'claim-cashback'],
  setup(props, { emit }) {
    const loading = ref(false);
    const processing = ref(false);
    const processingBurn = ref(false);
    const selectedAccounts = ref([]);
    const selectedBalanceAccounts = ref([]);
    const selectAll = ref(false);
    const selectAllWithBalance = ref(false);
    const rentAmount = ref(0.002); // SOL per account
    const showErrorModal = ref(false);
    const errorMessage = ref('');
    const showLimitWarningModal = ref(false);
    const showWalletAddressMismatchModal = ref(false);
    const showBurnWarningModal = ref(false);
    const showBurnLimitsWarningModal = ref(false);
    const MAX_ACCOUNTS_PER_TX = 10; // Максимальное количество ATA в одной транзакции
    const MAX_BURN_ACCOUNTS_PER_TX = 5; // Максимальное количество ATA с балансом в одной транзакции
    const activeTab = ref('empty'); // 'empty' или 'withBalance'
    // Простая пагинация
    const visibleEmptyCount = ref(50);
    const visibleBalanceCount = ref(50);
    const isAuthenticated = computed(() => store.getters.isAuthenticated); // Временно установлено в true для тестирования
    // const walletAddress = computed(() => store.getters.walletAddress);
    const { publicKey } = useWallet();
    
    // Разделяем аккаунты на две категории: с нулевым балансом и с ненулевым балансом
    const emptyAccounts = computed(() => {
      return props.accounts.filter(account => account.amount === 0);
    });
    
    const accountsWithBalance = computed(() => {
      return props.accounts.filter(account => account.amount > 0);
    });
    
    // Пагинация для пустых аккаунтов
    const displayedEmptyAccounts = computed(() => {
      return emptyAccounts.value.slice(0, visibleEmptyCount.value);
    });
    
    // Пагинация для аккаунтов с балансом
    const displayedBalanceAccounts = computed(() => {
      return accountsWithBalance.value.slice(0, visibleBalanceCount.value);
    });
    
    // Получение индекса аккаунта в оригинальном массиве
    const getOriginalIndex = (account) => {
      return props.accounts.findIndex(a => a.mint === account.mint);
    };
    
    // Загрузка дополнительных пустых аккаунтов
    const loadMoreEmptyAccounts = () => {
      visibleEmptyCount.value += 20;
    };
    
    // Загрузка дополнительных аккаунтов с балансом
    const loadMoreBalanceAccounts = () => {
      visibleBalanceCount.value += 20;
    };
    
    // Переключение выбора одного аккаунта
    const toggleSingleAccount = (index) => {
      // Если пытаемся выбрать, но уже достигнут лимит
      if (!selectedAccounts.value[index] && selectedCount.value >= MAX_ACCOUNTS_PER_TX) {
        showLimitWarningModal.value = true;
        return;
      }
      
      selectedAccounts.value[index] = !selectedAccounts.value[index];
      updateTotals();
    };

    const prepareStablecoin = (mint) => {
      const mints = {
        usdt: 'Es9vMFrzaCERmJfrF4H2FYD4KCoNkY11McCe8BenwNYB',
        usdc: 'EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v'
      }
      
      if (mints.usdt === mint) {
        return ' (USDT)';
      }
      
      if (mints.usdc === mint) {
        return ' (USDC)';
      }
      
      return '';
    }
    
    const toggleSingleBalanceAccount = (index) => {
      // Если пытаемся выбрать, но уже достигнут лимит
      if (!selectedBalanceAccounts.value[index] && selectedBalanceCount.value >= MAX_BURN_ACCOUNTS_PER_TX) {
        showLimitWarningModal.value = true;
        return;
      }
      
      // Проверяем, что у аккаунта не слишком большой баланс
      if (props.accounts[index].amount > 100000000) {
        return; // Не позволяем выбирать аккаунты с большим балансом
      }

      // Проверяем, что аккаунт не является стабильной валютой
      if (prepareStablecoin(props.accounts[index].mint) !== '') {
        return;
      }
      
      selectedBalanceAccounts.value[index] = !selectedBalanceAccounts.value[index];
      updateBalanceTotals();
    };
    
    // Initialize selectedAccounts array - select only accounts with zero balance by default (max 10)
    onMounted(() => {
      // Инициализация массива для пустых аккаунтов
      let selectedCount = 0;
      selectedAccounts.value = props.accounts.map(account => {
        // Выбираем только аккаунты с нулевым балансом, но не более MAX_ACCOUNTS_PER_TX
        if (account.amount === 0 && selectedCount < MAX_ACCOUNTS_PER_TX) {
          selectedCount++;
          return true;
        }
        return false;
      });
      
      // Инициализация массива для аккаунтов с балансом
      selectedBalanceAccounts.value = props.accounts.map(account => false);
      
      // Обновляем состояние чекбоксов "All"
      updateSelectAllState();
      
      // Автоматически выбираем первые 5 аккаунтов с балансом при загрузке
      selectAllWithBalance.value = true;
      toggleSelectAllWithBalance();
      
      // Проверяем, является ли пользователь владельцем BITCAT
      checkBitcatHolder();
    });
    
    const isBitcatHolder = ref(false);
    
    const checkBitcatHolder = async () => {
      try {
        // This would be your actual API endpoint to check if user holds BITCAT tokens
        // const response = await axios.get(`/api/user/is-bitcat-holder/${props.walletAddress}`);
        // isBitcatHolder.value = response.data.isHolder;
        
        // For now, we'll just set it to false
        isBitcatHolder.value = false;
      } catch (err) {
        console.error('Error checking BITCAT holder status:', err);
        isBitcatHolder.value = false;
      }
    };
    
    const feePercentage = computed(() => {
      return isBitcatHolder.value ? 0.1 : 0.2; // 10% for BITCAT holders, 20% for others
    });
    
    const selectedCount = computed(() => {
      return selectedAccounts.value.filter(selected => selected).length;
    });
    
    const selectedBalanceCount = computed(() => {
      return selectedBalanceAccounts.value.filter(selected => selected).length;
    });
    
    const totalReclaimableSol = computed(() => {
      const accountsWithoutBalance = props.accounts.filter(account => account.amount === 0);
      return (accountsWithoutBalance.length * rentAmount.value).toFixed(4);
    });
    
    const selectedReclaimableSol = computed(() => {
      return (selectedCount.value * rentAmount.value).toFixed(3);
    });
    
    const selectedBalanceReclaimableSol = computed(() => {
      return (selectedBalanceCount.value * rentAmount.value).toFixed(3);
    });
    
    const userReceiveAmount = computed(() => {
      const total = selectedCount.value * rentAmount.value;
      return total.toFixed(4);
    });
    
    const userReceiveBalanceAmount = computed(() => {
      const total = selectedBalanceCount.value * rentAmount.value;
      return total.toFixed(4);
    });
    
    const hasAccountsWithBalance = computed(() => {
      return props.accounts.some((account, index) => 
        selectedAccounts.value[index] && account.amount > 0
      );
    });

    const hasAccountsWithBalanceOnList = computed(() => {
      return props.accounts.some((account) => 
        account.amount > 0
      );
    });
    
    const toggleSelectAll = () => {
      if (selectAll.value) {
        // Если выбрано "Все", выбираем только аккаунты с нулевым балансом, но не более MAX_ACCOUNTS_PER_TX
        let zeroBalanceCount = 0;
        selectedAccounts.value = props.accounts.map((account) => {
          if(selectedAccounts.value.length == MAX_ACCOUNTS_PER_TX) {
            return false;
          }
          if (account.amount === 0 && zeroBalanceCount < MAX_ACCOUNTS_PER_TX) {
            zeroBalanceCount++;
            return true;
          }
          return false;
        });
        
        // Если есть больше аккаунтов с нулевым балансом, чем можно выбрать, показываем предупреждение
        // const totalZeroBalanceAccounts = props.accounts.filter(account => account.amount === 0).length;
        // if (totalZeroBalanceAccounts > MAX_ACCOUNTS_PER_TX) {
        //   showLimitWarningModal.value = true;
        // }
      } else {
        // Если снято "Все", снимаем выделение со всех аккаунтов
        selectedAccounts.value = props.accounts.map(() => false);
      }
      updateTotals();
    };
    
    const toggleSelectAllWithBalance = () => {
      if (selectAllWithBalance.value) {
        // Если выбрано "Все", выбираем только аккаунты с балансом, но не более MAX_BURN_ACCOUNTS_PER_TX
        let withBalanceCount = 0;
        selectedBalanceAccounts.value = props.accounts.map((account) => {
          if (prepareStablecoin(account.mint) !== '') {
            return false;
          }
          if (account.amount > 0 && account.amount < 100000000 && withBalanceCount < MAX_BURN_ACCOUNTS_PER_TX) {
            withBalanceCount++;
            return true;
          }
          return false;
        });
      } else {
        // Если снято "Все", снимаем выделение со всех аккаунтов
        selectedBalanceAccounts.value = props.accounts.map(() => false);
      }
      updateBalanceTotals();
    };
    
    const updateTotals = () => {
      updateSelectAllState();
    };
    
    const updateBalanceTotals = () => {
      updateSelectAllWithBalanceState();
    };
    
    const handleAccountSelection = (index) => {
      // Подсчитываем количество выбранных аккаунтов (true значения)
      const currentSelectedCount = selectedAccounts.value.filter(selected => selected).length;
      console.log('Selected accounts count:', currentSelectedCount);
      
      // Если пытаемся выбрать новый аккаунт, но уже выбрано максимальное количество
      if(currentSelectedCount > MAX_ACCOUNTS_PER_TX) {
        showLimitWarningModal.value = true;
        selectedAccounts.value[index] = false;
        return;
      }
      // Если пользователь пытается выбрать аккаунт, но уже выбрано MAX_ACCOUNTS_PER_TX
      const isAttemptingToCheck = !selectedAccounts.value[index];
      
      if (isAttemptingToCheck && currentSelectedCount >= MAX_ACCOUNTS_PER_TX) {
        // Предотвращаем выбор - оставляем как false
        // Vue автоматически обновит значение к тому, что выбрал пользователь, поэтому нам нужно вернуть значение обратно
        setTimeout(() => {
          selectedAccounts.value[index] = false;
        }, 0);
        // Показываем предупреждение
        showLimitWarningModal.value = true;
        return;
      }
      
      updateTotals();
    };
    
    const handleBalanceAccountSelection = (index) => {
      // Подсчитываем количество выбранных аккаунтов с балансом (true значения)
      const currentSelectedBalanceCount = selectedBalanceAccounts.value.filter(selected => selected).length;
      
      if(currentSelectedBalanceCount > MAX_BURN_ACCOUNTS_PER_TX) {
        showBurnLimitsWarningModal.value = true;
        selectedBalanceAccounts.value[index] = false;
        return;
      }
      // Если пользователь пытается выбрать аккаунт, но уже выбрано MAX_BURN_ACCOUNTS_PER_TX
      const isAttemptingToCheck = !selectedBalanceAccounts.value[index];
      
      if (isAttemptingToCheck && currentSelectedBalanceCount >= MAX_BURN_ACCOUNTS_PER_TX) {
        // Предотвращаем выбор - оставляем как false
        setTimeout(() => {
          selectedBalanceAccounts.value[index] = false;
        }, 0);
        // Показываем предупреждение о лимите для сжигания
        showBurnLimitsWarningModal.value = true;
        return;
      }
      
      // Проверяем, что у аккаунта не слишком большой баланс
      if (props.accounts[index].amount > 100000000) {
        // Не позволяем выбирать аккаунты с большим балансом
        setTimeout(() => {
          selectedBalanceAccounts.value[index] = false;
        }, 0);
        return;
      }

      // Проверяем, что аккаунт не является стабильной валютой
      if (prepareStablecoin(props.accounts[index].amount.mint) !== '') {
        setTimeout(() => {
          selectedBalanceAccounts.value[index] = false;
        }, 0);
        return;
      }
      
      updateBalanceTotals();
    };
    
    // Update selectAll checkbox state based on current selections
    const updateSelectAllState = () => {
      // Проверяем, выбраны ли все аккаунты с нулевым балансом (до MAX_ACCOUNTS_PER_TX)
      const zeroBalanceIndices = props.accounts.map((account, index) => account.amount === 0 ? index : -1).filter(i => i !== -1);
      
      // Если аккаунтов с нулевым балансом меньше или равно MAX_ACCOUNTS_PER_TX
      if (zeroBalanceIndices.length <= MAX_ACCOUNTS_PER_TX) {
        // Проверяем, выбраны ли все аккаунты с нулевым балансом
        const allZeroBalanceSelected = zeroBalanceIndices.every(index => selectedAccounts.value[index]);
        selectAll.value = allZeroBalanceSelected && zeroBalanceIndices.length > 0;
      } else {
        // Если аккаунтов с нулевым балансом больше MAX_ACCOUNTS_PER_TX, проверяем первые MAX_ACCOUNTS_PER_TX
        const firstNZeroBalanceIndices = zeroBalanceIndices.slice(0, MAX_ACCOUNTS_PER_TX);
        const allFirstNSelected = firstNZeroBalanceIndices.every(index => selectedAccounts.value[index]);
        selectAll.value = allFirstNSelected;
      }
    };
    
    // Update selectAllWithBalance checkbox state based on current selections
    const updateSelectAllWithBalanceState = () => {
      // Проверяем, выбраны ли все аккаунты с балансом (до MAX_BURN_ACCOUNTS_PER_TX)
      const balanceIndices = props.accounts.map((account, index) => (account.amount > 0 && account.amount < 100000000) ? index : -1).filter(i => i !== -1);
      
      // Если аккаунтов с балансом меньше или равно MAX_BURN_ACCOUNTS_PER_TX
      if (balanceIndices.length <= MAX_BURN_ACCOUNTS_PER_TX) {
        // Проверяем, выбраны ли все аккаунты с балансом
        const allBalanceSelected = balanceIndices.every(index => selectedBalanceAccounts.value[index]);
        selectAllWithBalance.value = allBalanceSelected && balanceIndices.length > 0;
      } else {
        // Если аккаунтов с балансом больше MAX_BURN_ACCOUNTS_PER_TX, проверяем первые MAX_BURN_ACCOUNTS_PER_TX
        const firstNBalanceIndices = balanceIndices.slice(0, MAX_BURN_ACCOUNTS_PER_TX);
        const allFirstNSelected = firstNBalanceIndices.every(index => selectedBalanceAccounts.value[index]);
        selectAllWithBalance.value = allFirstNSelected;
      }
    };
    
    const shortenAddress = (address) => {
      if (!address) return '';
      return `${address.slice(0, 4)}...${address.slice(-4)}`;
    };
    
    const claimCashback = async () => {
      processing.value = true;
      
      try {
        // Get selected mint addresses
        const selectedMints = props.accounts
          .filter((_, index) => selectedAccounts.value[index])
          .map(account => account.mint);
        
        if (selectedMints.length === 0) {
          throw new Error('No accounts selected');
        }
        
        // Connect to wallet if not connected
        if (!window.solana || !window.solana.isPhantom) {
          throw new Error('Phantom wallet not found. Please install Phantom extension.');
        }
        
        if (!window.solana.isConnected) {
          await window.solana.connect();
        }

        if(publicKey.value.toString() != props.walletAddress) {
          showWalletAddressMismatchModal.value = true;
          return;
        }
        
        // Get transaction instructions from backend
        const response = await axios.post('/ata-cashback/prepare-transaction', {
          walletAddress: props.walletAddress,
          mints: selectedMints
        });
        
        if (!response.data.success) {
          throw new Error(response.data.message || 'Failed to prepare transaction');
        }
        
        // Импортируем утилиту для работы с транзакциями
        const { signAndSendTransaction } = await import('@/utils/transactionUtils');
        
        // Подписываем и отправляем транзакцию с помощью нашей утилиты
        const result = await signAndSendTransaction({
          transaction: response.data.serializedTransaction,
          withPriority: false // Не используем приоритет для этой транзакции
        });
        
        if (!result.success) {
          throw new Error(result.error || 'Failed to process transaction');
        }
        
        // Send transaction to backend
        const confirmResponse = await axios.post('/ata-cashback/confirm-transaction', {
          walletAddress: props.walletAddress,
          signedTransaction: result.signedTransaction,
          accountsCount: selectedMints.length
        });
        
        if (!confirmResponse.data.success) {
          throw new Error(confirmResponse.data.message || 'Failed to confirm transaction');
        }
        
        // Emit success event
        emit('claim-cashback', {
          amount: selectedCount.value * rentAmount.value,
          accountsCount: selectedCount.value,
          sentSignature: confirmResponse.data.sentSignature
        });
        
        // Определяем индексы аккаунтов, которые нужно удалить
        const selectedIndices = [];
        selectedAccounts.value.forEach((selected, index) => {
          if (selected) {
            selectedIndices.push(index);
          }
        });
        
        // Создаем новый массив аккаунтов без использованных
        const remainingAccounts = [...props.accounts];
        // Сортируем индексы в обратном порядке, чтобы удаление не влияло на последующие индексы
        selectedIndices.sort((a, b) => b - a);
        selectedIndices.forEach(index => {
          remainingAccounts.splice(index, 1);
        });
        
        // Отправляем событие для обновления списка аккаунтов
        emit('update-accounts', remainingAccounts);
        
        // Сбрасываем выделения
        selectedAccounts.value = remainingAccounts.map(() => false);
        updateTotals();
        
      } catch (err) {
        console.error('Error claiming cashback:', err);
        // Показываем popup с ошибкой вместо alert
        errorMessage.value = err.response?.data?.message || 'Failed to process transaction';
        showErrorModal.value = true;
      } finally {
        processing.value = false;
      }
    };
    
    const burnAndClaimCashback = () => {
      // Показываем предупреждение перед сжиганием токенов
      showBurnWarningModal.value = true;
    };
    
    const confirmBurnAndClaimCashback = async () => {
      processingBurn.value = true;
      showBurnWarningModal.value = false;
      
      try {
        // Check if Phantom is available
        if (!window.solana) {
          throw new Error('Phantom wallet not found. Please install Phantom extension.');
        }
        
        if (!window.solana.isConnected) {
          await window.solana.connect();
        }

        if(publicKey.value.toString() != props.walletAddress) {
          showWalletAddressMismatchModal.value = true;
          return;
        }
        
        // Get selected mint addresses with balance
        const selectedMints = props.accounts
          .filter((_, index) => selectedBalanceAccounts.value[index])
          .map(account => account.mint);
        
        if (selectedMints.length === 0) {
          throw new Error('No accounts selected');
        }
        
        // Get transaction instructions from backend
        const response = await axios.post('/ata-cashback/prepare-transaction', {
          walletAddress: props.walletAddress,
          mints: selectedMints,
          withBurn: true
        });
        
        if (!response.data.success) {
          throw new Error(response.data.message || 'Failed to prepare transaction');
        }
        
        // Импортируем утилиту для работы с транзакциями
        const { signAndSendTransaction } = await import('@/utils/transactionUtils');
        
        // Подписываем и отправляем транзакцию с помощью нашей утилиты
        const result = await signAndSendTransaction({
          transaction: response.data.serializedTransaction,
          withPriority: false // Не используем приоритет для этой транзакции
        });
        
        if (!result.success) {
          throw new Error(result.error || 'Failed to process transaction');
        }
        
        // Send transaction signature to server for processing
        const confirmResponse = await axios.post('/ata-cashback/confirm-transaction', {
          walletAddress: props.walletAddress,
          signedTransaction: result.signedTransaction, // Отправляем подписанную транзакцию
          accountsCount: selectedMints.length,
          burn: true
        });
        
        if (!confirmResponse.data.success) {
          throw new Error(confirmResponse.data.message || 'Failed to confirm transaction');
        }
        
        // Emit success event
        emit('claim-cashback', {
          amount: selectedBalanceCount.value * rentAmount.value,
          accountsCount: selectedBalanceCount.value,
          sentSignature: confirmResponse.data.sentSignature
        });
        
        // Определяем индексы аккаунтов, которые нужно удалить
        const selectedIndices = [];
        selectedBalanceAccounts.value.forEach((selected, index) => {
          if (selected) {
            selectedIndices.push(index);
          }
        });
        
        // Создаем новый массив аккаунтов без использованных
        const remainingAccounts = [...props.accounts];
        // Сортируем индексы в обратном порядке, чтобы удаление не влияло на последующие индексы
        selectedIndices.sort((a, b) => b - a);
        selectedIndices.forEach(index => {
          remainingAccounts.splice(index, 1);
        });
        
        // Отправляем событие для обновления списка аккаунтов
        emit('update-accounts', remainingAccounts);
        
        // Сбрасываем выделения
        selectedBalanceAccounts.value = remainingAccounts.map(() => false);
        updateBalanceTotals();
        
      } catch (err) {
        console.error('Error burning and claiming cashback:', err);
        errorMessage.value = err.response?.data?.message || 'Failed to process burn transaction';
        showErrorModal.value = true;
      } finally {
        processingBurn.value = false;
      }
    };
    
    return {
      loading,
      processing,
      selectedAccounts,
      selectAll,
      rentAmount,
      selectedCount,
      totalReclaimableSol,
      selectedReclaimableSol,
      userReceiveAmount,
      hasAccountsWithBalance,
      hasAccountsWithBalanceOnList,
      handleAccountSelection,
      isBitcatHolder,
      toggleSelectAll,
      updateTotals,
      showErrorModal,
      errorMessage,
      showLimitWarningModal,
      MAX_ACCOUNTS_PER_TX,
      shortenAddress,
      claimCashback,
      burnAndClaimCashback,
      confirmBurnAndClaimCashback,
      handleBalanceAccountSelection,
      activeTab,
      emptyAccounts,
      accountsWithBalance,
      displayedEmptyAccounts,
      displayedBalanceAccounts,
      getOriginalIndex,
      loadMoreEmptyAccounts,
      loadMoreBalanceAccounts,
      toggleSingleAccount,
      isAuthenticated,
      feePercentage,
      processingBurn,
      selectedBalanceAccounts,
      selectedBalanceCount,
      selectedBalanceReclaimableSol,
      toggleSingleBalanceAccount,
      showBurnWarningModal,
      showBurnLimitsWarningModal,
      selectAllWithBalance,
      MAX_BURN_ACCOUNTS_PER_TX,
      toggleSelectAllWithBalance,
      showWalletAddressMismatchModal,
      prepareStablecoin
    };
  }
};
</script>

