<template>
  <div class="recent-history">
    <h3>Recent Cashback Transactions</h3>
    
    <div v-if="loading" class="loading">
      <i class="fas fa-spinner fa-spin"></i> Loading recent transactions...
    </div>
    
    <div v-else-if="error" class="error">
      <i class="fas fa-exclamation-circle"></i> {{ error }}
    </div>
    
    <div v-else-if="history.length === 0" class="no-data">
      <p>No recent transactions found</p>
    </div>
    
    <div v-else class="history-list">
      <div v-for="item in history" :key="item.id" class="history-item">
        <div class="history-row">
          <div class="left-section">
            <div class="user-section">
              <router-link class="user-tag" v-if="item.userTag" :to="`/player/${item.userTag}`">
                <i class="fas fa-user"></i> {{ item.userTag }}
              </router-link>
              <div class="user-tag" v-else>
                <i class="fas fa-user"></i> Guest User
              </div>
              
            </div>
            
            <div class="wallet-section">
              <i class="fas fa-wallet"></i>
              <span class="wallet-address">{{ formatWalletAddress(item.walletAddress) }}</span>
              <a v-if="item.sendTx" :href="`https://solscan.io/tx/${item.sendTx}`" target="_blank" class="tx-link">
                <span>TX </span><i class="fas fa-external-link-alt"></i>
              </a>
            </div>
          </div>
          
          <div class="right-section">
            
            <div class="amount">
              <div class="action-badges">
                <div class="ata-badge">
                  <span>{{ item.accountsCount }}</span>
                  <small>ATAs</small>
                </div>
                
                <div v-if="item.isBurn" class="type-badge burn">
                  <i class="fas fa-fire"></i>
                </div>
                
                <div class="type-badge reclaim">
                  <i class="fas fa-recycle"></i>
                </div>
              </div>

              <div class="amount-text">
                <i class="fas fa-coins"></i>
                <span>+{{ item.solAmount }}</span>
                <small>SOL</small>
              </div>
            </div>
            <span class="date">{{ formatDate(item.date) }}</span>



          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import axios from 'axios';

export default {
  name: 'RecentHistory',
  setup() {
    const history = ref([]);
    const loading = ref(true);
    const error = ref(null);
    
    const fetchRecentHistory = async () => {
      try {
        loading.value = true;
        error.value = null;
        
        const response = await axios.get('/ata-cashback/recent-history');
        if (response.data.success) {
          history.value = response.data.history;
        } else {
          error.value = 'Failed to load recent history';
        }
      } catch (err) {
        console.error('Error fetching recent history:', err);
        error.value = 'Error loading recent transactions';
      } finally {
        loading.value = false;
      }
    };
    
    const formatWalletAddress = (address) => {
      if (!address) return '';
      return `${address.substring(0, 4)}...${address.substring(address.length - 4)}`;
    };

    const formatDate = (date) => {
      if (!date) return '';
      const d = new Date(date);
      return d.toLocaleDateString('en-US', { 
        month: 'short', 
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    
    onMounted(() => {
      fetchRecentHistory();
    });
    
    return {
      history,
      loading,
      error,
      formatWalletAddress,
      formatDate
    };
  }
};
</script>

