<script setup>
import { ref } from 'vue'

const activeIndex = ref(-1)

const faqItems = [
  {
    question: "What are ATA Accounts?",
    answer: "ATA (Associated Token Accounts) are specialized accounts on Solana that hold tokens for a specific wallet address. When you close unused ATA accounts, you can reclaim the rent of <strong>0.002 SOL</strong> per account. This is a unique feature of the Solana blockchain that allows users to recover storage rent from accounts they no longer need."
  },
  {
    question: "Do I need to connect my wallet?",
    answer: "No, you don't need to connect your wallet for scanning! Our system can scan any wallet address to find unused ATA accounts. However, to actually close the selected accounts and reclaim your SOL, you will need to connect your wallet to sign the transactions."
  },
  {
    question: "What if you scam me?",
    answer: "We recommend reviewing our project thoroughly before starting. You can check our transaction history and see previous ATA closures where we successfully returned SOL to users. Transparency is important to us, and all transactions are verifiable on the Solana blockchain."
  },
  {
    question: "I still don't trust you, what should I do?",
    answer: "Security comes first! In the crypto world, caution is essential. We recommend connecting a wallet with minimal funds (around $0.1 is enough to sign transactions) but that may have unclosed ATA accounts. After using our service, feel free to disconnect your wallet for added security."
  },
  {
    question: "How much SOL can I get?",
    answer: "It depends entirely on how many unclosed ATA accounts you have. This number can range from zero to hundreds of accounts. Each closed account returns <strong>0.002 SOL</strong>. Simply scan your wallet address to see how many unused ATA accounts you have - no wallet connection required for scanning!"
  }
]

const toggleFAQ = (index) => {
  activeIndex.value = activeIndex.value === index ? -1 : index
}
